import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { useSelector } from "react-redux";
import { Typography } from "@mui/material";
import expandList from "../../assets/icons/expand.png";
import collapseList from "../../assets/icons/collapse.png";

export const TransfersTable = ({ rows, title }) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const [isAllTransfers, setIsAllTransfers] = React.useState(false);
  const [transfersToShow, setTransfersToShow] = React.useState([]);

  const isExpandList = isAllTransfers ? collapseList : expandList;

  React.useEffect(() => {
    if (isAllTransfers) setTransfersToShow(rows);
    else setTransfersToShow(rows.slice(0, 3));
  }, [isAllTransfers, rows]);

  return (
    <TableContainer component={Paper}>
      <Typography variant="h2">{title}</Typography>
      <Table  aria-label="sticky table">
        <TableHead>
          <TableRow hover role="checkbox">
            <TableCell>Date</TableCell>
            <TableCell>From</TableCell>
            <TableCell>To</TableCell>
            <TableCell>Amount</TableCell>
            <TableCell>Balance</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {transfersToShow.map((row) => (
            <TableRow
              hover={true}
              key={row._id}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell>
                {new Date(row.createdAt).toLocaleDateString()}
              </TableCell>
              {row.from === loggedUser.email ? (
                <TableCell>{loggedUser.fullname}</TableCell>
              ) : (
                <TableCell>{row.from}</TableCell>
              )}
              {row.to === loggedUser.email ? (
                <TableCell>{loggedUser.fullname}</TableCell>
              ) : (
                <TableCell>{row.to}</TableCell>
              )}
              <TableCell
                className={
                  loggedUser.email === row.to ? "amount-plus" : "amount-minus"
                }
              >
                {loggedUser.email === row.to
                  ? `+${row.transferAmount}`
                  : `-${row.transferAmount}`}
              </TableCell>
              {row.from === loggedUser.email ? (
                <TableCell>{row.fromBalance?.toFixed(2)}</TableCell>
              ) : (
                <TableCell>{row.toBalance?.toFixed(2)}</TableCell>
              )}
            </TableRow>
          ))}
        </TableBody>
      </Table>
      {rows.length > 3 && (
        <Typography
          component="button"
          onClick={() => setIsAllTransfers((prev) => !prev)}
          className="more-less-btn"
        >
          <img src={isExpandList} alt="" />
        </Typography>
      )}
    </TableContainer>
  );
};
